.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10rem 3rem 3rem;

    &__title {
        color: $color-grey-light-2;
        font-size: 3rem;
        font-weight: 800;
        text-shadow: 5px 2px 8px rgb(68, 68, 68);
        text-align: center;
    }

    &__media {
        display: flex;
        margin-top: 2rem;
    }
    
    &__icon {
        height: 4rem;
        width: 4rem;
        -webkit-mask-size: cover;
        mask-size: cover;
        background-color: black;
        
        &:hover {
            background-color: blue;
        }

        &:not(:last-child) {
            margin-right: 3rem;
        }
        &--linkedin {
            -webkit-mask-image: url(../../assets/images/linkedin.svg);
            mask-image: url(../../assets/images/linkedin.svg);
        }

        &--github {
            -webkit-mask-image: url(../../assets/images/github.svg);
            mask-image: url(../../assets/images/github.svg);
        }
    }
}